@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
:root {
  --primary: #263238;
  --secondary: #e91e63;
  --text: #fff;
  --bg: #90a4ae;
  --footer: #263238;
  --blackish: rgb(22, 22, 22);
}
p {
  margin: 0 !important;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Poppins", "Hind Siliguri", sans-serif;
}
tr {
  vertical-align: middle;
}
td,
th {
  min-width: 170px;
}

.footer {
  background: var(--blackish);
  color: var(--text);
  padding: 10px 0;
}
.footer-top {
  background: var(--footer);
}
.social-icons {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: flex-start;
}
.social-icons li {
  margin: 0 10px;
}
.social-icons li:nth-child(1) {
  margin-left: 0;
}
.social-icons li:nth-child(1) i {
  color: #ff0000;
}
.social-icons li:nth-child(2) i {
  color: #5374bd;
}
.social-icons li:nth-child(3) i {
  color: #8a3ab9;
}
.social-icons li:nth-child(4) i {
  color: #1da1f2;
}

.social-icons a {
  text-decoration: none;
}

.social-icons a i {
  font-size: 27px;
}
.col-title {
  color: white;
  text-transform: capitalize;
  margin: 25px 0 20px;
}

.information {
  list-style: none;
  margin: 0;
  padding: 0;
}

.information li {
  margin: 5px 0;
}
.information li a {
  text-decoration: none;
  color: #b0bec5;
  transition: 0.3s ease-in;
}
.information li a:hover {
  text-decoration: underline;
  color: var(--secondary);
}
.contact-info li {
  text-decoration: none;
  color: #b0bec5;
  transition: 0.3s ease-in;
}
.contact-info li i {
  margin-right: 5px;
}


.collections img {
  width: 100%;
}
.collections .img {
  padding: 0;
  position: relative;
  margin: 0 12px;
  width: 100%;
  height: 100%;
  display: block;
  margin-right: 0 !important;
}

.collections .img {
  background: #000;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.collections .img img {
  transition: all 0.8s;
}

.collections .img:hover img {
  transform: scale(1.1);
}

.collections .img::after {
  content: "";
  position: absolute;
  opacity: 0.3;
  width: 100%;
  display: block;
  height: 100%;
  top: 0;
  left: 0;
  padding: 0 12px;
  transition: 0.3s ease-in;
  background: rgba(0, 0, 0, 0.274);
}
.collections .col-lg-3.col-6:hover .img::after {
  opacity: 1;
  background: rgba(0, 0, 0, 0.274);
}
.feature {
  position: relative;
}
.feature::after {
  position: absolute;
  display: block;
  content: "";
  width: 150px;
  left: 50%;
  height: 2px;
  margin-top: 10px;
  transform: translateX(-50%);
  background-color: var(--secondary);
}

.testimonials {
  background: aliceblue;
  height: 700px;
}

.testimonials > div {
  position: relative;
  margin-top: 30px;
}

@media (max-width: 767px) {
  .testimonials > div {
    margin-top: 20px;
  }
}

.review-title > span {
  font-size: 1rem;
  color: #ed5217;
  text-transform: capitalize;
  display: block;
  margin-top: -0.6em;
}

.review-title h2 {
  margin: 0.3em 0 0;
  font-size: 2rem;
  font-weight: 600;
  color: #444;
  padding-bottom: 20px;
  margin-bottom: 50px;
  position: relative;
  margin-top: 15px;
}

.review-title {
  text-align: center;
  margin-bottom: 30px;
}

.review-title h2:before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 5rem;
  height: 3px;
  background: #ed5217;
  border-radius: 20px;
  left: 50% !important;
  transform: translateX(-50%);
}

.swiper-pagination {
  transform: translateY(10px);
}

.testimonials .swiper-pagination span {
  height: 10px;
  width: 10px;
  background: #fff;
  border: 2px solid #ed5217;
  margin: 0 5px;
}

.testimonials .swiper-pagination .swiper-pagination-bullet-active {
  background: #ed5217;
  height: 12px;
  width: 12px;
  margin-bottom: -1px;
}

.testimonials .card {
  background: #fff;
  box-shadow: 0 8px 30px -7px #c9dff0;
  margin: 0 20px;
  min-height: 340px;
  padding: 0 10px;
  border-radius: 20px;
  border: 0;
}

.testimonials .card .card-img-top {
  max-width: 100px;
  border-radius: 50%;
  margin: 15px auto 0;
  box-shadow: 0 8px 20px -4px #95abbb;
  width: 200px;
  height: 100px;
}

.testimonials .card h5 {
  color: #ed5217;
  font-size: 21px;
  line-height: 1.3;
}

.testimonials .card h5 span {
  font-size: 18px;
  color: #666666;
}

.testimonials .card p {
  font-size: 18px;
  color: #777;
  padding-bottom: 5px;
}

.testimonials .swiper-slide-next h5 {
  font-size: 24px;
}

.testimonials .swiper-slide-next h5 span {
  font-size: 20px;
}

.testimonials .swiper-slide-next .card-img-top {
  max-width: 100%;
  height: 120px;
  width: 120px;
}

.login-page {
  min-height: 100vh !important;
  background-image: linear-gradient(#00000049, #0000006e),
    url(/static/media/login.e26695b3.jpg);
  background-position: center center;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.formContainer {
  width: 100%;
  max-width: 450px !important;
}
.loginForm input {
  display: block;
  margin: 20px 0;
}

.login-btn {
  background: var(--secondary);
  color: var(--text);
  width: 100%;
  margin-top: 10px;
}
.login-btn:hover {
  color: var(--text);
}

.register-page {
  min-height: 100vh !important;
  background-image: linear-gradient(#00000049, #0000006e),
    url(/static/media/login.e26695b3.jpg);
  background-position: center center;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.formContainer {
  width: 100%;
  max-width: 450px !important;
}
.registerForm input {
  display: block;
  margin: 20px 0;
}

.register-btn {
  background: var(--secondary);
  color: var(--text);
  width: 100%;
  margin-bottom: 10px;
}
.register-btn:hover {
  color: var(--text);
}

.spinner-border {
  height: 20px !important;
  width: 20px !important;
  margin: 0 !important;
  padding: 0 !important;
}

.private-spinner .spinner-border {
  height: 200px !important;
  width: 200px !important;
}

.add-service .upload-btn {
  border: 2px solid #f8c59a;
  color: #fd7e15;
  max-width: 24rem;
}

.add-service .upload-btn:hover,
.add-service .upload-btn:active {
  border: 2px solid #fd7709 !important;
  color: white;
  background: #fd7709 !important;
  border-radius: 0.25rem;
}

.add-service .submit-btn {
    width: 9rem;
    padding: .68rem 0;
}

.admin-side-bar {
  max-width: 250px;
  margin-right: 25px;
  height: 90vh;
  border-right: none;
}
@media screen and (min-width: 768px) {
  .admin-side-bar {
    border-right: 1px solid #ddd;
  }
}
.admin-side-bar ul {
  margin-top: 20px;
  list-style: none;
}
@media screen and (max-width: 770px) {
  .admin-side-bar {
    height: auto !important;
  }
}
.admin-side-bar ul li {
  margin: 8px 0;
}
.admin-side-bar ul li a {
  color: #a3a3a3;
  text-decoration: none;
  font-size: 18px;
  transition: 0.5s;
}
.admin-side-bar ul li a:hover {
  color: #000;
}
.admin-side-bar ul li a i {
  margin-right: 10px;
}
.admin-container {
  margin-top: 20px;
}
.dashboard {
  overflow-x: hidden;
}
.active {
  color: #000 !important;
}

.navBar {
  background-color: var(--primary);
  color: #fff !important;
}

.navbar-nav a {
  font-weight: bold !important;
  color: #fff !important;
}
.navbar-nav a.current {
  color: var(--secondary) !important;
}
.navbar-nav a button.login-button {
  background: linear-gradient(
    335.64deg,
    #ff589b -19.22%,
    #ff136f 118.87%
  ) !important;
  padding: 8px 25px !important;
  color: #fff !important;
  border-radius: 52px !important;
  border: 0;
}
.ms-auto.navbar-nav {
  align-items: center;
}
@media screen and (max-width: 992px) {
  .ms-auto.navbar-nav {
    margin: auto;
    text-align: center;
  }
}

